.container {
  margin: 1px 0;
  height: 18px;
  width: 18px;
}
.checkbox[type="checkbox"] {
  margin: 0;
  /* Always hide the checkbox since we really don't want it to show */
  display: none !important;
}
.customInput {
  margin-bottom: 0;
  border: 1px solid #bdbcbd;
  background-color: #f7f7f7;
  background-repeat: no-repeat;
  background-position: center;
  transition: background-color 250ms linear;
  cursor: pointer;
  height: inherit;
  width: inherit;
  display: inline-block;
  border-radius: 4px;
}
.customInput::after {
  width: 0;
  height: 0;
  overflow: hidden;
  content: "";
  background-image: url("./assets/icon-checked.svg"), url("./assets/icon-indeterminate.svg");
}
.checkbox:checked + .customInput,
.checkbox.checked + .customInput {
  background-color: #4b57e6;
  border-color: #4b57e6;
  background-image: url("./assets/icon-checked.svg");
}
.checkbox:indeterminate + .customInput,
.checkbox.indeterminate + .customInput {
  background-image: url("./assets/icon-indeterminate.svg");
  border-color: #a5a5a5;
  background-color: #a5a5a5;
}
.checkbox:disabled + .customInput,
.checkbox.disabled + .customInput {
  opacity: 0.6;
  cursor: not-allowed;
}
